@import url(https://fonts.googleapis.com/css2?family=Outfit:wght@200;400;800&display=swap);
.movieShow{
    display: flex;
    flex-direction: column;
    margin-top: 1rem;    
    position: relative;  
    width: 300px;    
    overflow: hidden;  
    
}
.movieShow img {
    width: 100%;
    align-self: center;
    border: .1rem solid #010101;
}

.Yes{
    opacity: .35;
    -webkit-filter: grayscale(1);
            filter: grayscale(1);
}

.movieShow img:hover{
    -webkit-filter: grayscale(0);
            filter: grayscale(0);
    -webkit-transform: scale(.99);
            transform: scale(.99);
    opacity: 1;
    transition: all ease 0.4s;
    border: .1rem solid greenyellow;
}
.movieShow h3{
    padding:.5rem;
    margin:0;
    align-self: center;
    text-align: center;
}

.overviewInfo {
    position: absolute;
    opacity: .9;
    background-color: #111;
    bottom:0;
    left:0;
    right:0;
    max-height: 100%;
    -webkit-transform: translateY(100%);
            transform: translateY(100%);
    
}
.movieShow:hover .overviewInfo{
    cursor: pointer;
    transition: all ease 0.5s;
    -webkit-transform: translateY(0%);
            transform: translateY(0%);
}

.overviewInfo p{
    padding:.5rem;
    margin:0;
    align-self: center;
    text-align: justify;
}




.modal{
    text-align:justify;
    width: 80%;
    position: absolute;
        margin:0 auto;
        display: none;
        z-index: 99;
        left: 0;
        right:0;
        top: 58%; 
        padding: .25rem;
        background-color: #010101;
        -webkit-transform:translateY(-50%);
        transform:translateY(-50%);
}    

#poster{
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;

}

#description{
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    display: flex;
    flex-direction: column;
    grid-gap: 1rem;
    gap: 1rem;
    justify-content: space-evenly;
}

#title {
    padding: 0 2rem;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    color: white;
    text-align: center;
    font-size: 2.2rem;
    font-weight: 800;
}

#overview{
    padding: 2rem;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    font-size: 2rem;
}
@media only screen and (max-width: 1000px) {
    .modal {
      width: 70vw;
      
    }
    #title{
        font-size: 2rem;
    }

    #overview{
        font-size: 1.8rem;
    }

    #poster {
        width:30%;
    }
    

  }
@media only screen and (max-width: 800px) {
    .modal {
      width: 70vw;
      
    }
    #title{
        font-size: 1.5rem;
    }

    #overview{
        font-size: 1.3rem;
    }

    #poster {
        width:30%;
    }
    

  }
  @media only screen and (max-width: 550px) {
    .modal {
      width: 70vw;
      
    }
    #title{
        font-size: 1.2rem;
    }

    #overview{
        font-size: 1rem;
    }

    #poster {
        width:30%;
    }
    
  }

  @media only screen and (max-width: 550px) {
    .modal {
      width: 70vw;
      grid-gap: 1rem;
      gap: 1rem;
      flex-direction: column;
      
    }
    #title{
        font-size: 1rem;
    }

    #overview{
        font-size: .8rem;
    }

    #poster {
        width:100%;
    }
    
  }

html{
    background-color: #010101;
    color:whitesmoke;
    font-size: 62.5%;
    font-family: 'Outfit', sans-serif;
}


nav {
    box-sizing: border-box;
    display: flex;
    justify-content: space-between;
    height: 100px;
}

nav img {
    height:100%;
    margin-left: 10px;

}

img:hover {
    cursor:pointer;
}


nav ul{
    display: flex;
    grid-gap:2rem;
    gap:2rem;
    list-style: none;
    margin: 0;
    padding: 0;
    justify-content: center;
}

nav ul li {
    align-self: center;
    font-size:1.5em;
}

nav ul li:hover{
    color: greenyellow;
    cursor:pointer;
}

nav button {
    width: 100px;
    font-weight: 800;
    background-color:greenyellow;
    border:.1rem solid greenyellow;
}
nav button:hover {
    cursor:pointer;
    color: #111;
    background-color:greenyellow
}

section{
    display:flex;
    flex: 1 1;
    flex-wrap: wrap;
    grid-gap: 2rem;
    gap: 2rem;

    margin-top: 3rem;
    padding:0;
    justify-content: space-evenly;
}

footer {
    text-align: center;
    justify-content: center;
    position: relative;
    
}




